import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { DirektoriPegawaiSpService, DirektoriSpTipeService, DirektoriPegawaiMasterService, DirektoriHrNotesService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Pegawai_sp, Sp_tipe, Pegawai_master, Hr_notes } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';


@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private direktoriPegawaiSpService: DirektoriPegawaiSpService,
        private direktoriSpTipeService: DirektoriSpTipeService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private direktoriHrNotesService: DirektoriHrNotesService,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute
    ) { }

    data: Pegawai_sp = {}

    ngOnInit() {

        this.form()
        this.getPreeData()
    }

    listTipeSP: Sp_tipe[] = []
    listPegawai: Pegawai_master[] = []
    getPreeData() {
        this.direktoriSpTipeService.getDirektoriSpTipes().subscribe(
            data => {
                this.listTipeSP = data.data
            }
        )
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(
            data => {
                this.listPegawai = data.data
            }
        )

    }

    listNotes: Hr_notes[] = []
    getNotes() {
        this.direktoriHrNotesService.getDirektoriHrNotes(JSON.stringify({id_pegawai: this.dataForm.value.id_pegawai})).subscribe(
            data => {
                this.listNotes = data.data
            }
        )
    }


    dataForm: FormGroupTypeSafe<Pegawai_sp>
    form() {
        this.dataForm = this.fb.group<Pegawai_sp>(
            {
                id_pegawai: new FormControl('', [Validators.required]),
                id_sp_tipe: new FormControl('', [Validators.required]),
                tanggal_dikeluarkan: new FormControl(''),
                tanggal_evaluasi: new FormControl(''),
                pasal: new FormControl(''),
                keterangan: new FormControl(''),
                id_hr_notes: new FormControl(''),
            }
        )
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.direktoriPegawaiSpService.postDirektoriPegawaiSps(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/direktori/hr-management/peringatan/list'])
    }

}
