import { FormControl, Validators } from '@angular/forms'
import { Sp_tipe } from 'src/app/sdk/hr/models/sp-_tipe'
import { Pegawai_sp } from 'src/app/sdk/hr/models/pegawai-_sp'
import { Router, ActivatedRoute } from '@angular/router'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { NgxSpinnerService } from 'ngx-spinner'
import { DirektoriSpTipeService } from 'src/app/sdk/hr/services/direktori-sp-tipe.service'
import { DirektoriPegawaiSpService, DirektoriPegawaiMasterService, DirektoriHrNotesService } from 'src/app/sdk/hr/services'
import { Component, OnInit } from '@angular/core'
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper'
import { SingleUploadService } from 'src/app/components/document/single-upload.service'
import { Pegawai_master, Hr_notes } from 'src/app/sdk/hr/models'


@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private direktoriPegawaiSpService: DirektoriPegawaiSpService,
        private direktoriSpTipeService: DirektoriSpTipeService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private direktoriHrNotesService: DirektoriHrNotesService,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    data: Pegawai_sp = {}
    toogleUpload = false

    ngOnInit() {
        this.id = this.activatedRoute.snapshot.paramMap.get('id_pegawai_sp')

        this.form()
        this.getPreeData()
        this.getData()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'hrmanagement_sp.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    id: string = ''
    getData() {
        this.spinner.show()
        this.direktoriPegawaiSpService.getDirektoriPegawaiSpsId(this.id).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "hrmanagement_sp",
                    for_table: "erp_hr.pegawai_sp",
                    for_module_id: data.data.id_pegawai_sp
                })
            }
        )
    }

    listTipeSP: Sp_tipe[] = []
    listPegawai: Pegawai_master[] = []
    getPreeData() {
        this.direktoriSpTipeService.getDirektoriSpTipes().subscribe(
            data => {
                this.listTipeSP = data.data
            }
        )
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(
            data => {
                this.listPegawai = data.data
            }
        )

    }

    listNotes: Hr_notes[] = []
    getNotes() {
        this.direktoriHrNotesService.getDirektoriHrNotes(JSON.stringify({id_pegawai: this.dataForm.value.id_pegawai})).subscribe(
            data => {
                this.listNotes = data.data
            }
        )
    }


    dataForm: FormGroupTypeSafe<Pegawai_sp>
    form() {
        this.dataForm = this.fb.group<Pegawai_sp>(
            {
                id_pegawai_sp: new FormControl('', [Validators.required]),
                id_sp_tipe: new FormControl('', [Validators.required]),
                id_user: new FormControl(''),
                tanggal_dikeluarkan: new FormControl(''),
                tanggal_evaluasi: new FormControl(''),
                pasal: new FormControl(''),
                keterangan: new FormControl(''),
                id_dokumen: new FormControl(''),
                id_pegawai: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                id_data_flow_detail: new FormControl(''),
                id_data_flow: new FormControl(''),
                status: new FormControl(''),
                nomor: new FormControl(''),
                posisi: new FormControl(''),
                id_hr_notes: new FormControl(''),
            }
        )
    }
    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.direktoriPegawaiSpService.putDirektoriPegawaiSpsId({
            id: this.id,
            body: this.data
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/direktori/hr-management/peringatan/list'])
    }
}
