import { Sp_tipe } from 'src/app/sdk/hr/models/sp-_tipe';
import { DirektoriPegawaiService } from 'src/app/sdk/hr/services/direktori-pegawai.service';
import { Pegawai_sp } from 'src/app/sdk/hr/models/pegawai-_sp';
import { NgxSpinnerService } from 'ngx-spinner';
import { DirektoriPegawaiSpService, DirektoriPegawaiPosisiService, DirektoriVPegawaiPosisiService, DirektoriHrNotesService } from 'src/app/sdk/hr/services';
import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { Pegawai, V_pegawai_posisi, Hr_notes } from 'src/app/sdk/hr/models';
import { DirektoriSpTipeService } from 'src/app/sdk/hr/services/direktori-sp-tipe.service';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';
import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private direktoriPegawaiSpService: DirektoriPegawaiSpService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private direktoriSpTipeService: DirektoriSpTipeService,
        private direktoriHrNotesService: DirektoriHrNotesService,
        private router: Router,
        private spinner: NgxSpinnerService,
        private standarApprovalService: StandarApprovalService,
        private toolsLogFlowService: ToolsLogFlowService,
    ) { }

    id_pegawai_sp: string = ''

    pegawai_sp: Pegawai_sp = {}
    pegawai: Pegawai = {}
    pegawai_posisi: V_pegawai_posisi = {}
    sp_tipe: Sp_tipe = {}

    ngOnInit() {
        this.id_pegawai_sp = this.routerActive.snapshot.paramMap.get('id_pegawai_sp')
        console.log(this.id_pegawai_sp)
        this.getPegawaiSp();
        this.getLog()

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getPegawaiSp();
                    this.getLog()
                }
            }
        )

    }

    params: standarFlow = null;
    id_data_flow: string = ''
    getPegawaiSp() {
        this.spinner.show()
        this.direktoriPegawaiSpService.getDirektoriPegawaiSpsId(this.id_pegawai_sp).subscribe(
            response => {
                this.pegawai_sp = response.data
                this.getPegawai(response.data.id_pegawai)
                this.getPegawaiPosisi(response.data.id_pegawai)
                this.getSpTipe(response.data.id_sp_tipe)
                this.getNotes(response.data.id_hr_notes)

                this.params = {
                    id: response.data.id_pegawai_sp,
                    id_data_flow_detail: response.data.id_data_flow_detail,
                    id_data_flow: response.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_pegawai_sp"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()
            }
        )
    }

    dataNotes: Hr_notes = {}
    getNotes(id_hr_notes) {
        this.direktoriHrNotesService.getDirektoriHrNotesId(id_hr_notes).subscribe(
            response => {
                this.dataNotes = response.data
            }
        )
    }
    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai).subscribe(
            response => {
                this.pegawai = response.data
                this.spinner.hide()
            }
        )
    }

    getPegawaiPosisi(id_pegawai: string) {
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisisId(id_pegawai).subscribe(
            response => {
                this.pegawai_posisi = response.data
            }
        )
    }
    getSpTipe(id_sp_tipe: string) {
        this.direktoriSpTipeService.getDirektoriSpTipesId(id_sp_tipe).subscribe(
            response => {
                this.sp_tipe = response.data
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_pegawai_sp })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/hr-management/peringatan/list'])
    }

}
